.container {
    border: 1px solid;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 50px;
}

.social{
    display: flex;
    justify-content: space-between;
}

.icon{
    width: 30px;
    height: 30px;
    background-color: bisque;
    margin: 5px;
}

.footerText{
    color: white;
}